body {
  background-color: rgb(161, 221, 112);
  font-family: sans-serif;
  display: grid;
}

nav {
  background-color: rgb(238, 78, 78);
  display: flex;
  justify-content: space-around;
  padding: 20px;
  margin: 20px;
  font-weight: bold;
  align-items: center;
}

.navLink {
  text-decoration: none;
  color: black;
  font-size: 20px;
}

.navLink:hover {
  color: rgb(121, 147, 81);
  text-shadow: 1px 1px 1px black;
}

.product {
  display: flex;
  flex-wrap: wrap;
}

.container {
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
}

.product-card {
  padding: 2%;
  flex-grow: 1;
  flex-basis: 15%;
}

.hover-image-scale {
  display: inline-block;
  overflow: hidden;
  border-radius: 20px;
}
.hover-image-scale img {
  transition: 1s;
  display: block;
}
.hover-image-scale img:hover {
  transform: scale(1.2);
  border-radius: 20px;
}
.position {
  display: flex;
  flex-direction: column;
}
.btn {
  margin: 20px;
  padding: 15px;
  font-size: 20px;
  border: solid 1px rgb(238, 78, 78);
  text-shadow: 0.3px 0.3px rgb(238, 78, 78);
  box-shadow: 1px 1px 1px 1px rgb(238, 78, 78);
}

.btn:hover {
  transform: scale(1.1);
  background-color: rgb(161, 221, 112);
  color: rgb(246, 238, 201);
}

h1,
h2,
h3 {
  color: #1b1b1b;
}

.itemName {
  color: #1b1b1b;
  font-size: 20px;
  text-shadow: 0.5px 0.5px 0.5px #1b1b1b;
}
.cont {
  display: flex;
  justify-content: center;
}
.text {
  color: #1b1b1b;
  font-size: 25px;
}

.main {
  max-width: 100%;
  background-color: rgb(161, 221, 112);
  display: grid;
}
.main-container {
  max-width: 100%;
  margin-top: 50px;
}

.main-p {
  color: rgb(238, 78, 78);
  font-size: 20px;
  font-weight: 700;
  text-transform: uppercase;
  font-family: sans-serif;
}
.main-title {
  color: rgb(238, 78, 78);
  font-size: 60px;
  font-weight: 900;
  text-transform: uppercase;
  font-family: sans-serif;
}

.main-button {
  background-color: rgb(238, 78, 78);
  border-radius: 25px;
  font-size: 25px;
  color: #1b1b1b;
  font-weight: 700;
  padding: 10px 20px 10px 20px;
  margin-bottom: 70px;
}
.center {
  display: flex;
  justify-content: center;
}

.container-item {
  position: relative;
  overflow: hidden;
  width: 520px;
  height: 350px;
  background: rgb(246, 238, 201);
  box-shadow: 2px 2px 7px rgb(238, 78, 78);
  border-radius: 10px;
  margin-left: 50px;
}

p {
  font-size: 15px;
  color: #1b1b1b;
  font-weight: 400;
  letter-spacing: 1px;
}

h1 {
  font-size: 18px;
  color: #1b1b1b;
  margin-top: -5px;
}

h2 {
  color: #1b1b1b;
  margin-top: -5px;
}

img {
  width: 290px;
  margin-top: 47px;
}

.slideshow-buttons {
  top: 70%;
  display: none;
}

.one,
.two,
.three,
.four {
  position: absolute;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: rgb(246, 238, 201);
}

.one {
  left: 22%;
}
.two {
  left: 27%;
}
.three {
  left: 32%;
}
.four {
  left: 37%;
}

.product-info {
  position: absolute;
  width: 40%;
  height: 100%;
  top: 10%;
  left: 60%;
}

.info {
  text-transform: none;
  letter-spacing: 0;
  margin-bottom: 13px;
  color: #1b1b1b;
  font-size: 0.7em;
  line-height: 1.6em;
  margin-right: 25px;
  text-align: justify;
}

.add {
  background: #1b1b1b;
  padding: 10px;
  display: inline-block;
  outline: 0;
  border: 0;
  margin: -1px;
  border-radius: 2px;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: rgb(246, 238, 201);
  cursor: pointer;
  width: 67%;
}

.add:hover {
  background: rgb(246, 238, 201);
  transition: all 0.4s ease-in-out;
  border: 1px solid #1b1b1b;
  color: #1b1b1b;
}

.like {
  width: 22%;
}

.qties {
  display: grid;
  color: #1b1b1b;
  grid-template-columns: repeat(auto-fill, 30px);
  width: 60%;
  grid-gap: 4px;
  margin-left: 20px;
  margin-top: 5px;
}

.qties:hover {
  cursor: pointer;
}

.select {
  margin-top: 11px;
  margin-bottom: 0;
  margin-left: 20px;
}

.qty {
  padding: 9px;
  border: 1px solid #1b1b1b;
  font-size: 0.7em;
  text-align: center;
}
.size:hover {
  background: #1b1b1b;
  color: rgb(246, 238, 201);
  transition: all 0.4s ease-in-out;
}


ul {
  margin: 0px;
  padding: 0px;
}
.footer-section {
  position: relative;
  margin-top: 100px;
  background-color: rgb(161, 221, 112);
}
.container-footer {
  margin-left: 50px;
  margin-right: 50px;
}

.footer-content {
  position: relative;
  z-index: 2;
}
.footer-pattern img {
  position: absolute;
  top: 0;
  left: 0;
  height: 330px;
  background-size: cover;
  background-position: 100% 100%;
}

.footer-social-icon span {
  color: #1b1b1b;
  display: block;
  font-size: 20px;
  font-weight: 700;
  font-family: sans-serif;
  margin-bottom: 20px;
}
.footer-social-icon a {
  color: #1b1b1b;
  font-size: 16px;
  margin-right: 15px;
}

.footer-widget-heading h3 {
  color: #1b1b1b;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 40px;
  position: relative;
}
.footer-widget-heading h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -15px;
  height: 2px;
  width: 50px;
}
.footer-widget ul li {
  display: inline-block;

  width: 50%;
  margin-bottom: 12px;
}

.footer-widget ul li a {
  color: #1b1b1b;
  text-transform: capitalize;
  text-decoration: none;
}
.subscribe-form {
  position: relative;
  overflow: hidden;
}
.subscribe-form input {
  width: 100%;
  padding: 14px 28px;
  border: 1px solid rgb(238, 78, 78);
  color: rgb(246, 238, 201);
}
.subscribe-form button {
  position: absolute;
  right: 0;
  padding: 13px 20px;
  border: 1px solid rgb(238, 78, 78);
  top: 0;
}
.subscribe-form button i {
  color: rgb(161, 221, 112);
  font-size: 22px;
  transform: rotate(-6deg);
}

.footer-icon {
  height: 50px;
  width: 50px;
}
.icon {
  height: 30px;
  width: 30px;
  color: #1b1b1b;
}